<script>
import HeaderComponent from "@/components/NotInstalledComponents/HeaderComponent.vue";
import TitleComponent from "@/components/NotInstalledComponents/TitleComponent.vue";
import ProofsComponent from "@/components/NotInstalledComponents/ProofsComponent.vue";
import SliderComponent from "@/components/NotInstalledComponents/SliderComponent.vue";
import AboutComponent from "@/components/NotInstalledComponents/AboutComponent.vue";
import CheapsComponent from "@/components/NotInstalledComponents/CheapsComponent.vue";
import RatingComponent from "@/components/NotInstalledComponents/RatingComponent.vue";
import RatingStatsComponent from "@/components/NotInstalledComponents/RatingStatsComponent.vue";
import CommentsComponent from "@/components/NotInstalledComponents/CommentsComponent.vue";
import FooterComponent from "@/components/NotInstalledComponents/FooterComponent.vue";
import InstalledScreenComponent from "@/components/InstalledComponents/InstalledScreenComponent.vue";


export default {
  components: {
    InstalledScreenComponent,
    FooterComponent,
    CommentsComponent,
    RatingStatsComponent,
    RatingComponent,
    CheapsComponent, AboutComponent, SliderComponent, ProofsComponent, TitleComponent, HeaderComponent},
  data() {
    return {
      checkInstall: false,
      showInstall: true,
      deferredPrompt: null,
      inAppBrowser: false,
      click_id: '',
      click_price: '',
      widget_id: '',
      teaser_id: '',
      campaign_id: '',
      category_id: '',
      user_id: '',
      geo: '',
      geo_region: '',
      ifa: '',
      price_range: '',
      title: ''
    };
  },
  computed: {
    installStatus() {
      return this.install ? 'Установлено' : 'Не установлено';
    }
  },
  mounted() {
    let checkInstallWindow = localStorage.getItem("install", 'true')
    this.checkInstall = checkInstallWindow;
    this.inAppBrowser = this.checkInAppBrowser();
    window.addEventListener("beforeinstallprompt", (event) => {
      event.preventDefault();
      this.deferredPrompt = event;
    });
  },
  methods: {
    goTOoffer() {
      this.showInstall = false;
    },
    installPWA() {
      if (this.deferredPrompt) {
        this.deferredPrompt.prompt();
        this.deferredPrompt.userChoice.then((choiceResult) => {
          if (choiceResult.outcome === "accepted") {
            this.setTLocalStorage();
            this.showInstall = false;
          } else {
            console.log("User dismissed the PWA install prompt");
          }
          this.deferredPrompt = null;
        });
      }
    },
    setTLocalStorage() {
      const getParams = new URLSearchParams(window.location.search)
      localStorage.setItem("install", 'true')
      localStorage.setItem("key", getParams.get("key"))
      localStorage.setItem("click_id", getParams.get("click_id"))
      localStorage.setItem("click_price", getParams.get("click_price"))
      localStorage.setItem("widget_id", getParams.get("widget_id"))
      localStorage.setItem("teaser_id", getParams.get("teaser_id"))
      localStorage.setItem("compaign_id", getParams.get("compaign_id"))
      localStorage.setItem("category_id", getParams.get("category_id"))
      localStorage.setItem("user_id", getParams.get("user_id"))
      localStorage.setItem("geo", getParams.get("geo"))
    },
    checkInAppBrowser() {
      const userAgent = navigator.userAgent || navigator.vendor || window.opera;
      return (
          userAgent.includes('FBAN') ||
          userAgent.includes('FBAV') ||
          userAgent.includes('Instagram') ||
          userAgent.includes('Twitter')
      );
    },
  },
};
</script>

<template>
  <div class="wrap">
    <HeaderComponent/>
    <div v-if="!checkInstall || showInstall">
      <div class="installContainer">
        <TitleComponent/>
        <ProofsComponent/>
        <div class="button-container">
          <button v-if="deferredPrompt" @click="installPWA" id="installBtn" class="installBtn">
            Install btn Support PWA TEST
            <!---->
          </button>
          <button v-if="!deferredPrompt || inAppBrowser" @click="goTOoffer" id="installBtn" class="installBtn">
            Install btn NOT Support PWA TEST
            <!---->
          </button>
          <!---->
        </div>
        <SliderComponent/>
        <AboutComponent/>
        <CheapsComponent/>
        <RatingComponent/>
        <RatingStatsComponent/>
        <CommentsComponent/>
        <FooterComponent/>
      </div>
    </div>
    <div v-if="checkInstall || !showInstall">
      <InstalledScreenComponent/>
    </div>
    <!---->
  </div>
</template>



<style lang="scss">
@import "styles.css";

</style>
