<script>
export default {
  name: "InstalledScreenComponent",
  data() {
    return {
      redirectLink: 'https://www.1win.com/?key=',
      key: "",
      click_id: "",
      click_price: "",
      widget_id: "",
      teaser_id: "",
      campaign_id: "",
      category_id: "",
      user_id: "",
      geo: "",

    }
  },
  async mounted() {
    const getKey = await localStorage.getItem('key');
    const getClickId = await localStorage.getItem('click_id');
    const getClickPrice = await localStorage.getItem('click_price');
    const getWidgetId = await localStorage.getItem('widget_id');
    const getTeaserId = await localStorage.getItem('teaser_id');
    const getCompaignId = await localStorage.getItem('compaign_id');
    const getCategoryId = await localStorage.getItem('category_id');
    const getUserId = await localStorage.getItem('user_id');
    const getGeo = await localStorage.getItem('geo');
    try {
      this.key = getKey;
      this.click_id = getClickId;
      this.click_price = getClickPrice;
      this.widget_id = getWidgetId;
      this.teaser_id = getTeaserId;
      this.campaign_id = getCompaignId;
      this.category_id = getCategoryId;
      this.user_id = getUserId;
      this.geo = getGeo;
      let url = "https://20bet.com/?key="+encodeURIComponent(this.key)+'&click_id='+encodeURIComponent(this.click_id)+'&click_price='+ encodeURIComponent(this.click_price)+'&widget_id='+encodeURIComponent(this.widget_id)+'&teaser_id='+encodeURIComponent(this.teaser_id) +'&campaign_id='+encodeURIComponent(this.campaign_id)+'&category_id='+encodeURIComponent(this.category_id)+'&user_id='+encodeURIComponent(this.user_id)+'&geo='+encodeURIComponent(this.geo);
      setTimeout(() => {
        window.location.href = url
      }, 0);
    } catch(e) {
      console.log(e)
    }
  }
}
</script>
